import React, { Component } from 'react'

import facebook from '../media/socials/facebook.png';
import instagram from '../media/socials/instagram.png';
import pinterest from '../media/socials/pinterest.png';

import HomeIcon from '@material-ui/icons/Home';
import StoreIcon from '@material-ui/icons/Store';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import PermMediaIcon from '@material-ui/icons/PermMedia';
import InfoIcon from '@material-ui/icons/Info';
import ContactSupportIcon from '@material-ui/icons/ContactSupport';
import CategoryIcon from '@material-ui/icons/Category';
import DashboardIcon from '@material-ui/icons/Dashboard';
import PersonIcon from '@material-ui/icons/Person';
import BookIcon from '@material-ui/icons/Book';
import MeetingRoomIcon from '@material-ui/icons/MeetingRoom';

const DelayLink = React.lazy(() => import('./DelayLink'));
const axios = require('axios');

export class Nav extends Component {
    render() {
        return (
            <nav className={`${this.props.nav === null ? 'gone' : (this.props.nav ? 'animate__animated animate__slideInRight' : 'animate__animated animate__slideOutRight')}`}>
                <ul style={{margin: this.props.loggedIn ? '4vh auto 0 auto' : '8vh auto 0 auto'}}>
                    <li>
                        <DelayLink same={this.props.toggleNav} delay={600} to="/" onDelayStart={() => this.props.runLoader()}><HomeIcon/>Home</DelayLink>
                    </li>
                    <li>
                        {!this.props.loggedIn ? 
                        <React.Fragment>
                            <DelayLink same={this.props.toggleNav} delay={600} to="/login" onDelayStart={() => this.props.runLoader()}><AccountCircleIcon/>Log In</DelayLink>
                        </React.Fragment>
                        :
                        <React.Fragment>
                            <DelayLink same={this.props.toggleNav} delay={600} to="/dashboard" onDelayStart={() => this.props.runLoader()}><DashboardIcon/>{this.props.admin ? "Admin" : "Dashboard"}</DelayLink>
                        </React.Fragment>
                        }
                    </li>
                    {this.props.admin &&
                    <li>
                        <DelayLink same={this.props.toggleNav} delay={600} to="/editStore" onDelayStart={() => this.props.runLoader()}><PermMediaIcon/>Edit Store</DelayLink>
                    </li>}
                    <li>
                        <DelayLink same={this.props.toggleNav} delay={600} to="/packages" onDelayStart={() => this.props.runLoader()}><CategoryIcon/>Packages</DelayLink>
                    </li>
                    {!this.props.admin &&                     
                    <li>
                        <DelayLink same={this.props.toggleNav} delay={600} to="/bookings" onDelayStart={() => this.props.runLoader()}><BookIcon/>Bookings</DelayLink>
                    </li>}
                    <li>
                        
                        <DelayLink same={this.props.toggleNav} delay={600} to="/store" onDelayStart={() => this.props.runLoader()}><StoreIcon/>Store</DelayLink>
                    </li>
                    <li>
                        <DelayLink same={this.props.toggleNav} delay={600} to="/about" onDelayStart={() => this.props.runLoader()}><PersonIcon/>About</DelayLink>
                    </li>
                    <li>
                        <DelayLink same={this.props.toggleNav} delay={600} to="/contact" onDelayStart={() => this.props.runLoader()}><ContactSupportIcon/>Contact</DelayLink>
                    </li>
                    <li>
                        <DelayLink same={this.props.toggleNav} delay={600} to="/info" onDelayStart={() => this.props.runLoader()}><InfoIcon/>Site Info</DelayLink>
                    </li>
                    {this.props.loggedIn && 
                    <li onClick={() => {axios.get('/logout'); this.props.checkLoggedIn(); window.location.href = 'https://omarhillphotos.com/login'}}>
                        <DelayLink same={this.props.toggleNav} delay={600} to="/info" onDelayStart={() => this.props.runLoader()}><MeetingRoomIcon/>Logout</DelayLink>
                    </li>
                    }
                </ul>
                <div className="socials">
                    <a href="https://www.facebook.com/omarhillphotos" target="_blank" rel="noreferrer"><img src={facebook} alt="facebook"/></a>
                    <a href="https://www.instagram.com/omarhillphotos/" target="_blank" rel="noreferrer"><img src={instagram} alt="instagram"/></a>
                    <a href="https://www.pinterest.com/omarhillphotos/" target="_blank" rel="noreferrer"><img src={pinterest} alt="pinterest"/></a>
                </div>
            </nav>
        )
    }
}

export default Nav
